<template>
    <div>
         <!-- 面包屑导航 -->
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>用户管理</el-breadcrumb-item>
        </el-breadcrumb>
        <el-card class="box-card">
          <!-- 卡片头部 -->
            <div slot="header" class="clearfix">
              <span>用户管理</span>
            </div>
            <!-- 卡片内容区头部 -->
              <el-row>
                <el-col :span="3">
                     <el-button type="primary" icon="el-icon-circle-plus-outline" size="middle" plain @click="addUserHandle"  >新增用户</el-button>
                </el-col>
              </el-row>
          <!-- 卡片内容区 -->
          <el-table  :data="userTableData"  style="width: 100%" border stripe>
            <el-table-column   prop="userType" label="用户类型"   width="180">
              <template slot-scope="scope">
                  <el-tag v-if="scope.row.userType===0">超级管理员</el-tag>
                  <el-tag type="success" v-else-if="scope.row.userType===1" >管理员</el-tag>
                  <el-tag type="warning" v-else>普通用户</el-tag>
              </template>
            </el-table-column>
            <el-table-column  prop="userName" label="用户名"   width="180"></el-table-column>
            <el-table-column  prop="userUpdateDate"   label="修改时间">  </el-table-column>
            <el-table-column   label="操作">
              <template slot-scope="scope">
                  <el-button type="text"  size="mini" v-if="userType === 0" @click="editUserInfo(scope.row.userId,scope.row.userName,scope.row.userType)">修改</el-button>
                  <el-button type="text"  size="mini" v-else-if="userType=== 1 " @click="editUserInfo(scope.row.userId,scope.row.userName,scope.row.userType)">修改</el-button>
                  <el-button type="text"  size="mini" v-else>查看</el-button>
              </template>
            </el-table-column>
          </el-table>
          <!-- 卡片内容分页区 -->
             <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 30, 40]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalNum">
             </el-pagination>

        </el-card>

        <!-- 点击新增用户 弹出对话框 -->
      <el-dialog  title="新增用户" :visible.sync="addUserDialogVisible" width="50%" :before-close="handleClose" >
         <el-form :model="addUserRuleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
              <!-- <el-form-item label="客户名称" prop="CustomerName">
                客户名称
              </el-form-item> -->
              <el-form-item label="用户类型" prop="userType">
                <el-select v-model="addUserRuleForm.userType" placeholder="请选择用户类型">
                  <el-option label="管理员" value="1"></el-option>
                  <el-option label="普通用户" value="2"></el-option>
                   <el-option label="超级管理员" value="0"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="用户名称" prop="userName">
                <el-input v-model="addUserRuleForm.userName" class="el-input-width"></el-input>
              </el-form-item>
               <el-form-item label="用户密码" prop="userPassword">
                <el-input v-model="addUserRuleForm.userPassword" class="el-input-width" autocomplete="off" type="password"></el-input>
              </el-form-item>
              <el-form-item label="确认密码" prop="checkPassword">
                <el-input v-model="addUserRuleForm.checkPassword" class="el-input-width" autocomplete="off" type="password"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="addUserSubmitForm('ruleForm')" plain>立即新增</el-button>
                <el-button @click="addUserResetForm('ruleForm')" plain type="danger">重置</el-button>
              </el-form-item>
          </el-form>
      </el-dialog>
<!-- 修改用户 信息 -->
       <el-dialog  title="修改用户" :visible.sync="editUserDialogVisible" width="50%" :before-close="editHandleClose" >
         <el-form :model="editUserRuleForm" :rules="editRules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
              <!-- <el-form-item label="客户名称" prop="CustomerName">
                客户名称
              </el-form-item> -->
              <el-form-item label="用户类型" prop="userType">
                <el-select disabled v-model="editUserRuleForm.userType" placeholder="请选择用户类型">
                  <el-option label="管理员" value="1"></el-option>
                  <el-option label="普通用户" value="2"></el-option>
                   <el-option label="超级管理员" value="0"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="用户名称" prop="userName">
                <el-input v-model="editUserRuleForm.userName" class="el-input-width" disabled></el-input>
              </el-form-item>
               <el-form-item label="用户密码" prop="userPassword">
                <el-input v-model="editUserRuleForm.userPassword" class="el-input-width" autocomplete="off" type="password"></el-input>
              </el-form-item>
              <el-form-item label="确认密码" prop="checkPassword">
                <el-input v-model="editUserRuleForm.checkPassword" class="el-input-width" autocomplete="off" type="password"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="editUserSubmitForm('ruleForm')" plain>立即修改</el-button>
                <el-button @click="editUserResetForm('ruleForm')" plain type="danger">重置</el-button>
              </el-form-item>
          </el-form>
      </el-dialog>
    </div>
</template>
<script>
import Qs from 'qs'
export default {
  data () {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.addUserRuleForm.checkPassword !== '') {
          this.$refs.ruleForm.validateField('checkPassword')
        }
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.addUserRuleForm.userPassword) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    var editValidatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.editUserRuleForm.checkPassword !== '') {
          this.$refs.ruleForm.validateField('checkPassword')
        }
        callback()
      }
    }
    var editValidatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.editUserRuleForm.userPassword) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      userTableData: [],
      // 登录用户的类型
      userType: null,
      // 新增用户对话框的显示与隐藏
      addUserDialogVisible: false,
      // 新增用户表单
      // 新增用户信息数组
      addUserRuleForm: {
        userType: '',
        userName: '',
        userPassword: '',
        checkPassword: ''
      },
      rules: {
        userType: [
          { required: true, message: '请选择用户类型', trigger: 'change' }
        ],
        userName: [
          { required: true, message: '请填写用户名称', trigger: 'blur' }
        ],
        userPassword: [
          { validator: validatePass, trigger: 'blur' }
        ],
        checkPassword: [
          { validator: validatePass2, trigger: 'blur' }
        ]
      },
      // 总数据条数
      totalNum: null,
      // 页面数据条数
      pageSize: null,
      // 当前页数
      currentPage: 1,
      editUserDialogVisible: false,
      // 新增用户信息数组
      editUserRuleForm: {
        userType: '',
        userName: '',
        userPassword: '',
        checkPassword: ''
      },
      editRules: {
        userType: [
          { required: true, message: '请选择用户类型', trigger: 'change' }
        ],
        userName: [
          { required: true, message: '请填写用户名称', trigger: 'blur' }
        ],
        userPassword: [
          { validator: editValidatePass, trigger: 'blur' }
        ],
        checkPassword: [
          { validator: editValidatePass2, trigger: 'blur' }
        ]
      },
      editUserId: null,
      editUserName: '',
      editUserType: null
    }
  },
  created () {
    this.userType = window.sessionStorage.getItem('userType') - 0
    // 获取用户列表
    this.getUserList()
  },
  methods: {
    editUserInfo (userId, userName, userType) {
      this.editUserId = userId
      this.editUserName = userName
      this.editUserRuleForm.userName = userName
      this.editUserRuleForm.userType = userType + ''
      this.editUserDialogVisible = true
    },
    handleCurrentChange (newPage) {
      this.currentPage = newPage
      this.getUserList()
    },
    handleSizeChange (newSize) {
      this.pageSize = newSize
      this.getUserList()
    },
    // 关闭新增用户对话框
    handleClose () {
      this.addUserResetForm('ruleForm')
      this.addUserDialogVisible = false
    },
    addUserSubmitForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          // 调用新增用户接口
          const data = {
            userName: this.addUserRuleForm.userName,
            userPassword: this.addUserRuleForm.userPassword,
            userType: this.addUserRuleForm.userType,
            userId: window.sessionStorage.getItem('userId')
          }

          const data1 = Qs.stringify(data)
          const { data: res } = await this.$http.post('/user/addUser', data1)
          if (res.status === 200) {
            this.addUserDialogVisible = false
            this.addUserResetForm('ruleForm')
            this.getUserList()
            return this.$message.success('新增用户成功！')
          }
          if (res.status === 500) return this.$message.error('当前用户已存在！')
        } else {
          return false
        }
      })
    },
    addUserResetForm (formName) {
      this.$refs[formName].resetFields()
    },
    // 点击新增用户按钮触发该事件
    addUserHandle () {
      this.addUserDialogVisible = true
    },
    editUserSubmitForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          // 调用修改用户接口
          const data = {
            userId: this.editUserId,
            userPassword: this.editUserRuleForm.checkPassword
          }

          const data1 = Qs.stringify(data)
          const { data: res } = await this.$http.post('/user/updateUser', data1)
          if (res.status !== 200) return this.$message.error(res.msg)
          this.editUserResetForm('ruleForm')
          this.editUserDialogVisible = false
          this.$message.success(res.msg)
        } else {
          // console.log('error submit!!')
          return false
        }
      })
    },
    editUserResetForm (formName) {
      this.$refs[formName].resetFields()
    },
    // 点击新增用户按钮触发该事件
    editUserHandle () {
      this.editUserDialogVisible = true
    },
    editHandleClose () {
      this.editUserDialogVisible = false
      this.editUserResetForm('ruleForm')
    },
    // 获取用户列表
    async getUserList () {
      const data = Qs.stringify({ pageSize: this.pageSize, pageNum: this.currentPage })
      const { data: res } = await this.$http.post('/user/findUser', data)
      this.totalNum = res.data.totalNum - 0
      this.pageSize = res.data.pageSize - 0
      this.currentPage = res.data.currentPage
      this.userTableData = res.data.items
      if (res.status !== 200) return this.$message.error('获取用户列表失败！')
    }
  }
}
</script>

<style lang="less" scoped>
// 表单输入框宽度
@el-input-width:90%;
.el-input-width{
    width: @el-input-width!important;
}
</style>
