<template>
    <div>
      <!-- 面包屑导航 -->
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item :to="{path:'/my/myclient'}">我的客户</el-breadcrumb-item>
          <el-breadcrumb-item>联系人</el-breadcrumb-item>
        </el-breadcrumb>
      <!-- 卡片视图区 -->
        <el-card class="box-card">
          <!-- 卡片头部 -->
            <div slot="header" class="clearfix">
              <span>联系人</span>
              <el-button style="float: right; padding: 3px 0" type="text" icon="el-icon-d-arrow-left" @click="returnHandle">返回</el-button>
            </div>

          <!-- 卡片内容区 -->
            <div>
            <!-- 卡片内容区头部 -->
              <el-row>
                <el-col :span="3">
                     <el-button type="primary" icon="el-icon-circle-plus-outline" size="middle" plain @click="addLinkmanHandle">新增</el-button>
                </el-col>
                <el-col :span="6">
                </el-col>
              </el-row>
            <!-- 卡片内容表单区 -->
              <el-row>
                <el-table  :data="customerTableData" style="width: 100%" stripe  border >
                  <el-table-column  type="index" width="50"> </el-table-column>
                  <el-table-column    prop="contactPersonName"   label="联系人" width="130"  > </el-table-column>
                  <el-table-column  prop="contactPersonPhone"    label="电话" width="180"  >
                    <template slot-scope="scope">
                      {{scope.row.contactPersonPhone?scope.row.contactPersonPhone:'---'}}
                    </template>
                  </el-table-column>
                  <el-table-column    prop="contactPersonWechat"   label="微信" width="180"  >
                    <template slot-scope="scope">
                      {{scope.row.contactPersonWechat?scope.row.contactPersonWechat:'---'}}
                    </template>
                  </el-table-column>
                  <el-table-column    prop="contactPersonWangwang"   label="阿里旺旺" width="180"  >
                      <template slot-scope="scope">
                      {{scope.row.contactPersonWangwang?scope.row.contactPersonWangwang:'---'}}
                    </template>
                  </el-table-column>
                  <el-table-column  prop="contactPersonUpdatedDate"  label="更新时间"    > </el-table-column>
                </el-table>
              </el-row>
          <!-- 卡片内容分页区 -->
            <!-- <el-pagination  :page-sizes="[100, 200, 300, 400]" :page-size="100" layout="total, sizes, prev, pager, next, jumper"  :total="400"> </el-pagination> -->
        </div>
      </el-card>

      <!-- 新增联系人弹框 -->
      <el-dialog  title="新增联系人" :visible.sync="addLinkmanDialogVisible" width="50%" :before-close="handleClose">
         <el-form :model="addLinkmanRuleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
              <!-- <el-form-item label="客户名称" prop="CustomerName">
                客户名称
              </el-form-item> -->
              <el-form-item label="联系人" prop="contactPersonName">
                <el-input v-model="addLinkmanRuleForm.contactPersonName" class="el-input-width"></el-input>
              </el-form-item>
               <el-form-item label="电话" prop="contactPersonPhone">
                <el-input v-model="addLinkmanRuleForm.contactPersonPhone" class="el-input-width"></el-input>
              </el-form-item>
               <el-form-item label="微信" prop="contactPersonWechat">
                <el-input v-model="addLinkmanRuleForm.contactPersonWechat" class="el-input-width"></el-input>
              </el-form-item>
               <el-form-item label="阿里旺旺号" prop="contactPersonWangwang">
                <el-input v-model="addLinkmanRuleForm.contactPersonWangwang" class="el-input-width"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="addLinkmanSubmitForm('ruleForm')" plain>立即新增</el-button>
                <el-button @click="addLinkmanResetForm('ruleForm')" plain type="danger">重置</el-button>
              </el-form-item>
          </el-form>
      </el-dialog>

    </div>
</template>
<script>
import Qs from 'qs'
export default {
  data () {
    return {
      // 表格数组
      customerTableData: [],
      // 控制新增联系人弹框的显示和隐藏
      addLinkmanDialogVisible: false,
      // 新增表单信息数组
      addLinkmanRuleForm: {
        contactPersonPhone: '',
        contactPersonWechat: '',
        contactPersonWangwang: '',
        contactPersonName: '',
        customerId: ''
      },
      rules: {
        contactPersonName: [
          { required: true, message: '请填写客户名称', trigger: 'blur' }
        ]
      }

    }
  },
  created () {
    this.customeId = this.$route.params.id
    // 获取联系人列表
    this.getLinkman()
  },
  methods: {
    async getLinkman () {
      const data = Qs.stringify({ customerId: this.$route.params.id })
      const { data: res } = await this.$http.post('/contactPerson/findContactPerson', data)
      if (res.status !== 200) return this.$message.error('获取联系人失败！')
      this.customerTableData = res.data
    },
    // 点击新增联系人按钮触发该事件
    addLinkmanHandle () {
      this.addLinkmanDialogVisible = true
    },
    async addLinkeman () {
      // 调用新增联系人接口
      this.addLinkmanRuleForm.customerId = this.$route.params.id
      const data = Qs.stringify(this.addLinkmanRuleForm)
      const { data: res } = await this.$http.post('/contactPerson/addContactPerson', data)
      if (res.status === 200) {
        this.addLinkmanDialogVisible = false
        this.addLinkmanResetForm('ruleForm')
        this.getLinkman()
        return this.$message.success('新增联系人成功！')
      }
    },
    addLinkmanSubmitForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          let flag = this.addLinkmanRuleForm.contactPersonPhone.trim() || this.addLinkmanRuleForm.contactPersonWechat.trim() || this.addLinkmanRuleForm.contactPersonWangwang.trim()
          if (!flag) return this.$message.error('至少输入一种联系方式！')
          // 验证手机号的规则
          const regMobile = /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/
          if (this.addLinkmanRuleForm.contactPersonPhone.trim()) {
            if (regMobile.test(this.addLinkmanRuleForm.contactPersonPhone.trim())) {
              this.addLinkeman()
            } else {
              return this.$message.error('请输入合法得手机号！')
            }
          } else {
            // 调用新增客户接口
            this.addLinkeman()
          }
        } else {
          return this.$message.error('提交失败！')
        }
      })
    },
    addLinkmanResetForm (formName) {
      this.$refs[formName].resetFields()
    },
    // 点击返回按钮触发该事件，返回上一页
    returnHandle () {
      this.$router.push('/my/myclient')
    },
    handleClose () {
      this.addLinkmanResetForm('ruleForm')
      this.addLinkmanDialogVisible = false
    }
  }
}
</script>
<style lang="less" scoped>
// 表单输入框宽度
@el-input-width:90%;
.el-input-width{
    width: @el-input-width!important;
}
</style>
